/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Elections in Lower Saxony and Hessen","date":"02 Feb 2003","slug":"/2003-02-02-elections-in-lower-saxony-and-hessen/","tags":"Y2003"},"html":"<p>Five years ago, the local elections in Lower Saxony were a resounding landslide for Gerhard Schröder, his crowning as the Chancellor candidate who would eventually defeat Helmut Kohl. Today, the election was a resounding defeat for his successor and protégé Sigmar Gabriel, and a victory for Christian Wulff, the quiet CDU leader who twice lost to Schröder. As a resident of Lower Saxony, I can certainly live with a Premier Wulff.</p>\n<p>The CDU has, as expected, <a href=\"http://www.dw-world.de/english/0,3367,1430_A_769775_1_A,00.html\">won historic victories</a> in <em>Landtag</em> elections Lower Saxony and in Hessen. The Union has over 48% in both states, and may have absolute majorities in one if not both local parliaments. The SPD suffered huge losses, at or near post-war lows. Greens and FDP gained in both states. </p>\n<p>Schröder tried to play the Iraq card (he announced his 'no' in the UN Security Council at a campaign rally in Goslar), but voters didn't buy it. For one, the card has been already played, and for another, voters are smart enough to know that foreign policy is made in Berlin, not in Hannover and Wiesbaden.</p>\n<p>I disagree with <a href=\"http://chicagoboyz.blogspot.com/2003_02_01_chicagoboyz_archive.html#88419844\">Ralf Goergens</a> that this is the beginning of the end for Schröder. The Bundestag elections are too fresh, and the leftists in the SPD have no alternative. I'm afraid the only way to get rid of Schröder is at the polls in 2006.</p>"},"next":{"fields":{"title":"I Can't Tell You","date":"01 Feb 2003","slug":"/2003-02-01-i-cant-tell-you/"}},"previous":{"fields":{"title":"Columbia Lost","date":"04 Feb 2003","slug":"/2003-02-04-columbia-lost/"}}},{"node":{"fields":{"title":"I Can't Tell You","date":"01 Feb 2003","slug":"/2003-02-01-i-cant-tell-you/","tags":"Y2003"},"html":"<p>We had a privacy issue crop up in regards to PapaScott, and I took the archives down until I could think of a good solution. Now they are back up, and I've made a subtle change so that certain people looking for a certain thing won't find it here. I won't tell you what it is. It's a privacy issue.</p>"},"next":{"fields":{"title":"Put It In A Letter","date":"31 Jan 2003","slug":"/2003-01-31-put-it-in-a-letter/"}},"previous":{"fields":{"title":"Elections in Lower Saxony and Hessen","date":"02 Feb 2003","slug":"/2003-02-02-elections-in-lower-saxony-and-hessen/"}}},{"node":{"fields":{"title":"Put It In A Letter","date":"31 Jan 2003","slug":"/2003-01-31-put-it-in-a-letter/","tags":"Y2003"},"html":"<p>If you want to get involved in grassroot politics, you have to start small. You can join the local branch of a political party, organize petition drives, talk to your neighbors, express your views to your congressman or MP, and write letters to the editor. And maybe, just maybe, you can make a small difference. Unless your name is  Aznar, Blair, or Berlusconi, then your <a href=\"http://www.opinionjournal.com/extra/?id=110002994\">letter</a> can make a huge difference. </p>\n<p>An issue I almost always raise on my posts on foreign policy is whether Europe matters? The warbloggers say no, but constantly whine about Europe anyway. (Does that make them warwhiners?) Gerhard Schröder said no, and that made it OK for him to use the Iraq crisis for domestic purposes. The French seem to say no, except insofar it can help them reassert their influence in the world and the EU. Donald Rumsfeld's 'old Europe' remarks seemed to imply that he wished Europe would matter more. The Aznar letter is a sound reply that yes, Europe does matter. </p>\n<p>Thomas Nephew <a href=\"http://www.papascott.de/2003/01/24/2080.php#comments\">commented</a> on my small mammal post that \"[It seems] like the more important repercussions of the Iraq crisis are the transatlantic ones.\" I think so, and I think this letter is the first step to clearing things up. The transatlantic partnership is important. Stop. It is too important to abuse for domestic or EU politics. Double stop.</p>"},"next":{"fields":{"title":"Return of Winter","date":"30 Jan 2003","slug":"/2003-01-30-return-of-winter/"}},"previous":{"fields":{"title":"I Can't Tell You","date":"01 Feb 2003","slug":"/2003-02-01-i-cant-tell-you/"}}}],"pathPrefix":"","first":false,"last":false,"index":790,"pageCount":1101}}

/***/ })

